<template>
  <div class="shop">
    <!-- 头部 -->
    <div class="header">
      <div class="header-left" @click="back">
        <span class="iconfont icon-xiangzuo"></span>
      </div>
      <div class="header-center">
        <p v-if="carList.length==0">购物车</p>
        <p v-else>购物车({{carList.length}})</p>
      </div>
      <div class="header-right" >
        <span class="iconfont icon-menu" v-if="carList.length"></span>
      </div>
    </div>

    <!-- 无商品时显示 -->
     <div v-if="!carList.length">
      <div class="empty-car">
        <img src="img/shop1.png" alt='' />
        <p>购物车内暂时没有商品</p>
        <router-link to="/index">去逛逛</router-link>
      </div>
      <shop-pro></shop-pro>
    </div>

    <!-- 有商品时显示 -->
    <div v-else>
      <div class="full-car">
        <div class="car-item" v-for="(item,index) in carList" :key="index">
          <div class="item-left" @click="select(index)">
            <span class="iconfont icon-xuanzhong"  v-if="item.flag"></span>
            <span class="iconfont icon-quan" v-else></span>
            
          </div>
          <div class="item-right">
            <div class="item-img">
              <img :src="item.img" alt="">
            </div>
            <div class="item-detail">
              <p>{{item.name}}</p>
              <div class="detail-num">
                <span>数量:</span>
                <div>
                  <p @click='sub(index)'>
                    <span class="iconfont icon-jian1" v-if='item.reduceFlag'></span>
                    <i class="iconfont icon-shanchu2" v-else></i>
                    <span></span>
                  </p>
                  <input type="text" v-model="item.thisNum">
                  <p @click='add(index)'>
                    <span class="iconfont icon-jia1"></span>
                  </p>
                </div>
              </div>
            </div>
            <div class="item-footer">
              <span>￥{{item.newPrice}}</span>
              <span @click="delOne(index)">删除</span>
            </div>
          </div>
        </div>
      </div>
    </div>
   
   <!-- 结算 -->
    <div class="footer" v-if="carList.length">
      <div class="footer-left">
        <div class="footer-checkAll" @click="selectAll">
          <span class="iconfont icon-xuanzhong" v-if="selectData"></span>
          <span class="iconfont icon-quan" v-else></span>
          全选
        </div>
        <span @click="delAll">删除</span>
      </div>
      <span class="footer-center">
        合计:
        <span class="total">￥{{total.price}}</span>
      </span>
      <button class="footer-right" type="button" id="jieSuan">去结算({{total.num}})</button>
    </div>
    
    <!-- 模态框 -->
    <div class="delModelbox" v-if='delModel.flag'>
      <div class="delModel">
        <p class="delModel-text">确认删除这个商品吗?</p>
        <div class="delModel-option">
          <p @click="delModel.flag=false">取消</p>
          <p @click="confirm(delModel.num)">确定</p>
        </div>
      </div>
    </div>

    <div class="delModelbox" v-if='delModelAll'>
      <div class="delModel">
        <p class="delModel-text">确认删除这{{total.num}}个商品吗?</p>
        <div class="delModel-option">
          <p @click="delModelAll=false">取消</p>
          <p @click="confirmAll">确定</p>
        </div>
      </div>
    </div>

  </div>
</template>

<script>

import shopPro from "../components/shop/shopProduct.vue";
export default {
  data() {
    return {
      carList: [],
      selectData:false,
      delModel:{
        flag:false,
        num:''
      },
      delModelAll:false
    }
  },
  created() {
    this.carList=this.$store.state.shopcarList;
    if(this.carList.length>0){
      this.$emit('getFlag',false)
    }else{
      this.$emit('getFlag',true)
    }
    this.addflag();
  },
  methods: {
    back(){
      this.$router.go(-1);
    },
    addflag(){
      for (const i in this.carList) {
        this.$set(this.carList[i], "flag", false);
      }
    },
    add(index){
      this.carList[index].thisNum++
      this.$store.commit("changeList",this.carList);
      if(this.carList[index].thisNum>1){
        this.carList[index].reduceFlag=true
     }
    },
    sub(index){
      if(this.carList[index].thisNum>1){
          this.carList[index].thisNum--
          this.$store.commit("changeList",this.carList);
          if(this.carList[index].thisNum==1){
            this.carList[index].reduceFlag=false
         }
      }else{
         this.delModel.flag=true
      }     
    },
    select(index){
      this.carList[index].flag = !this.carList[index].flag;
      let list=this.carList.filter((val)=>{
        return val.flag==true
      })
      if(list.length== this.carList.length){
        this.selectData=true;
      }else{
        this.selectData=false;
      }
    },
    selectAll(){
      this.selectData=!this.selectData;
      for (let i = 0; i < this.carList.length; i++) {
        if(this.selectData){
          this.carList[i].flag=true;
        }else{
          this.carList[i].flag=false;
        }        
      }
    },
    delOne(index){
      this.delModel.flag = true;
      this.delModel.num = index;
    },
    confirm(index){
      console.log(index)
      this.carList.splice(index,1);
      this.delModel.flag = false;
      this.$store.commit("changeList",this.carList);
    },
    delAll(){
      let list=this.carList.filter((val)=>{
        return val.flag==true
      })
      if(list.length!=0){
        this.delModelAll = true;
      }    
    },
    confirmAll(){
      this.carList=this.carList.filter((val)=>{
        return val.flag==false
      })
      this.delModelAll = false;
      this.$store.commit("changeList",this.carList);
    }
  },
  computed: {
    total(){
      let newCarList = this.carList.filter(item => {
        return item.flag == true;
      });
      if(newCarList==0){
        return {
          price:0,
          num:0
        }
      }else{
        let totalPrice=0;
        for (let i = 0; i < newCarList.length; i++) {
          totalPrice+= newCarList[i].thisNum*newCarList[i].newPrice;        
        }
        return{
          price:totalPrice,
          num:newCarList.length
        }
      }
    }
  },
   watch: {
    "carList.length": {
      handler(newVal) {
        if (newVal > 0) this.$emit("getFlag", false);
        else this.$emit("getFlag", true);
      }
    }
  },
  components:{
    shopPro
  }
};
</script>

<style lang="less" scoped>
.icon-xuanzhong{
  color:#FF734C;
}
  .header{
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .header-left{
      width: 20%;
      margin-left: 10px;
      span{
        display: block;
        font-size: 25px;
      }
    }
    .header-center{
      width: 60%;
      text-align: center;
      p{
        font-size: 18px;
      }
    }
    .header-right{
      width: 20%;
      text-align: right;
      margin-right: 10px;
      span{
        display: block;
        font-size: 25px;
      }
    }
  }
  .empty-car{
    padding: 60px 0;
    text-align: center;
    box-shadow: 0 5px 10px 0 #e9ecf0;
    p{
      font-size: 18px;
    }
    a{
      display: inline-block;
      width: 140px;
      height: 40px;
      line-height: 40px;
      background: #ff734c;
      border-radius: 30px;
      color: #ffffff;
      font-size: 18px;
      font-weight: 600;
    }
  }
  .full-car{
    padding-bottom: 50px;
    .car-item{
      display: flex;
      border-bottom: 1px solid #e9ecf0;
      height: 150px;
      .item-left{
        width: 10%;
        line-height: 150px;
        text-align: center;
        span{
          font-size: 24px;
        }
      }
      .item-right{
        width: 90%;
        position: relative;
        height: 120px;
        display: flex;
        .item-img{
          width: 35%;
          box-sizing: border-box;
          img{
           width: 100%;
           height: 100%;
          }
        }
        .item-detail{
          padding: 0 15px;
          &>p{
            margin-top: 15px; 
            font-size: 16px;
          }
          .detail-num{
            display: flex;
            margin-top: 15px;
            div{
              margin-left: 10px;
              p{
                display: inline-block;
                width: 25px;
                height: 22px;
                border: 1px solid #e9ecf0;           
              }
              input{
                width: 60px;
                height: 20px;
                outline: none;
                text-align: center;
                border: 0;
                border: 1px solid #e9ecf0;
                border-left: none;
                border-right: none;
              }
            }            
          }
        }
        .item-footer{
          &>span:nth-child(1){
            position: absolute;
            left: 150px;
            top: 100px;
            color: #FF734C;
            font-size: 16px;
          }
          &>span:nth-child(2){
            position: absolute;
            left: 300px;
            top: 100px;
            color: #FF734C;
            font-size: 16px;
          }
        }
      }      
    }    
  }
  .footer{
    width: 100%;
    height: 60px;
    position: fixed;
    left: 0;
    bottom: 0;
    background: #fff;
    display: flex;
    line-height: 60px;
    .footer-left{
      display: flex;
      width: 35%;
      .footer-checkAll{
        font-size: 18px;
        padding-left: 5px;
        span{
          font-size: 24px;
        }
      }
      &>span{
        color: #ccc;
        margin-left: 15px;
        font-size: 18px;
      }
    }
    .footer-center{
      display: inline-blcok;
      width: 35%;
      text-align: right;
      padding-right: 12px;
      font-size: 18px;
    }
    .footer-right{
      width: 30%;
      color: white;
      font-size: 22px;
      background-color: #FF734C;
    }
  }
  .delModelbox {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
    background-color: rgba(0, 0, 0, 0.7);
    .delModel {
      background-color: #fff;
      font-size: 14px;
      text-align: center;
      width: 60%;
      margin: 50% auto 0;
      border-radius: 10px;
      .delModel-text {
        color: #71797f;
        font-size: 15px;
        line-height: 80px;
      }
      .delModel-option {
        display: flex;
        p {
          width: 50%;
          box-sizing: border-box;
          line-height: 50px;
          border-top: 1px solid #d0d0d0;
          &:nth-child(1) {
            border-right: 1px solid #d0d0d0;
            color: #ff734c;
          }
        }
      }
    }
  }
</style>
